import axios from "axios";
import { useState } from "react";
import "./App.css";

import { UilSnowflake } from '@iconscout/react-unicons';
import { UilMoon } from '@iconscout/react-unicons';
import { UilRaindropsAlt } from '@iconscout/react-unicons';
import { UilCloudShowersHeavy } from '@iconscout/react-unicons';
import { UilSun } from '@iconscout/react-unicons';
import { UilAnalysis } from '@iconscout/react-unicons';
import { UilInstagram } from '@iconscout/react-unicons';
import { UilGithub } from '@iconscout/react-unicons';
import { UilEnvelopeShare } from '@iconscout/react-unicons'



function App() {
  const ApiKey = "a36f0c7460c051cf843a337046d3c27a";
  const [Data, setData] = useState({});
  const [InputCity, setInputCity] = useState("Pinjore");

  const getWeatherDeatails = (cityName) => {
    const apiUrl =
      "https://api.openweathermap.org/data/2.5/weather?q=" +
      cityName +
      "&units=metric&appid=" +
      ApiKey;
    axios
      .get(apiUrl)
      .then((res) => {
        console.log("response", res);
        setData(res.data);
      })
      .catch((err) => {
        console.log("err", err);
      });
  };

  const handleInputChange = (e) => {
    setInputCity(e.target.value);
  };
  const handleSearch = () => {
    getWeatherDeatails(InputCity);
  };

  return (
    <>
      <div
        className="mx-auto max-w-screen-xl mt-4 py-5 px-32 bg-gradient-to-br from-cyan-700 to-blue-700
      h-fit shadow-xl shadow-gray-800"
      >
        
            <div className="  text-center text-white font-extrabold text-2xl  tracking-wide sm:text-4xl">Weather App  </div>
           
             <div className="flex flex-col my-5">

             <input
                type="text"
                className="rounded-3xl py-1 px-2 text-center bg-gray-200 shadow-2xl shadow-black"
                onChange={handleInputChange}
              />
              <button
                className="bg-red-600 text-white max-w-lg px-4 py-2 my-2 rounded-2xl m-auto hover:bg-red-500"
                type="button"
                onClick={handleSearch}
              >
                Search
              </button>
             </div>
            
              
                   <div className="flex flex-row justify-around text-gray-200 text-3xl  font-semibold tracking-wider sm:3xl">
                   <h3 className="mx-3">{Data.name} </h3>
                   <UilAnalysis size="80"/>
                    <h5>Country:{Data?.sys?.country}</h5>
                    
                   </div>
                  
                  <div className="flex flex-row  flex-wrap justify-around py-11">

                   <div className="bg-gray-300 p-4 font-bold rounded-3xl  tracking-wide text-cyan-900 my-3 mx-3">
                  
                   <h3 className="">
                   <UilSnowflake size="70"/>
                   <h6>(Temperature)</h6>
                    {Data?.main?.temp} C </h3>
                    <h6>feels_like <br /> {Data?.main?.feels_like} C</h6>
                    
                   </div>
                 

                  <div className="bg-gray-300 p-4 font-bold rounded-3xl tracking-wide text-cyan-900 my-5 mx-3">
                  <UilSun size="80"/>
                  <h3 className="weatherData">{Data?.main?.pressure} </h3>
                    <h6>(Pressure)</h6>
                  </div>
                  
                   <div className="bg-gray-300 p-4 font-bold rounded-3xl tracking-wide text-cyan-900 my-5 mx-3">
                   <UilCloudShowersHeavy size="80"/>
                   <h3 className="weatherData">{Data?.main?.humidity} </h3>
                    <h6>(Humidity)</h6>
                  
                   </div>
                   <div className="bg-gray-300 p-4 font-bold rounded-3xl tracking-wide text-cyan-900 my-5 mx-3">
                   <UilRaindropsAlt size="80"/>
                   <h3 className="weatherData">{Data?.wind?.speed} </h3>
                    <h6>(Wind Speed)</h6>
                   </div>
                  
                   <div className="bg-gray-300 p-4 font-bold rounded-3xl tracking-wide text-cyan-900 my-5 mx-3">
                   <UilMoon   size="80"/>
                   <h3 className="weatherData">{Data?.visibility} metres</h3>
                    <h6>(Visibility)</h6>
                   </div>
                    </div>
                    
                   </div>
                   <div className=" flex bg-cyan-300 max-w-screen-xl m-auto text-2xl font-extrabold text-gray-100 py-2 px-3">Contact me : </div>
                   <div className="flex flex-row  justify-around bg-cyan-300  py-6 m-auto max-w-screen-xl ">
                    
                     
                    <a href="https://www.instagram.com/mevinaykaushik/"><UilInstagram size="50"/></a>
                    <a href="https://github.com/mevinaykaushik"><UilGithub size="50"/></a>
                    <a href = "mailto:vinaykaushik3696@gmail.com"><UilEnvelopeShare size="50"/></a>
                    
                    
                    
                      
                      
                    </div>
    </>
  );
}

export default App;
